.contacts {
  padding: var(--big-offset) 0;
  color: var(--color-main);
  background-color: #f3f6f6;

  &__container {
    display: flex;
    align-items: start;
  }

  &__title {
    margin-bottom: var(--big-offset);
  }

  &__item {
    font-style: normal;
  }

  &__subtitle {
    margin: 0;
    margin-bottom: var(--main-offset);
    font: var(--small-font);
    text-transform: uppercase;
    color: var(--color-gray);
  }

  &__phone {
    display: inline-block;
    margin-bottom: var(--small-offset);
    font: var(--middle-high-bold);
    color: var(--color-main);
  }

  &__text {
    margin-right: var(--second-offset);
    width: calc(100% - (680px + var(--second-offset)));
  }

  &__address {
    display: block;
    margin-bottom: var(--main-offset);
  }

  &__map {
    width: 680px;
    height: 500px;
  }
}
